  font-style: normal; }

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

html, body {
  width: 100%;
  height: 100%;
  line-height: 1.42;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
  font-family: 'Ubuntu', Arial, Helvetica, sans-serif;
  color: #2f3234; }

.visuallyhidden:not(:focus):not(:active) {
  position: absolute;
  width: 0.0625rem;
  height: 0.0625rem;
  margin: -0.0625rem;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: normal; }

.wrapper {
  overflow: hidden;
  width: 100%;
  height: 100%; }

.maincontent {
  height: 100%; }

ul {
  list-style-type: none; }

.welcome {
  width: 100%;
  height: 100%;
  display: flex;
  position: relative;
  background: url("../img/welcome/welcome-bg.png") center center no-repeat;
  background-size: cover;
  align-items: center;
  justify-content: center; }

.welcome__auth {
  position: absolute;
  text-align: center;
  top: 24px;
  right: 100px;
  width: 180px;
  height: 45px;
  border: 1px solid #ffffff;
  color: #ffffff;
  border-radius: 12px;
  font-family: Roboto;
  font-size: 16px;
  font-weight: 500;
  line-height: 45px;
  text-decoration: none; }

.entry {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 380px;
  min-height: 395px;
  background-color: rgba(18, 38, 32, 0.6); }

.entry__img {
  margin-top: 40px;
  width: 141px;
  height: 141px;
  border-radius: 100%;
  background: url("../img/welcome/people-one.jpg") center center no-repeat; }

.entry__name {
  text-align: center;
  color: #ffffff;
  font-family: Roboto;
  font-size: 28px;
  font-weight: 500;
  line-height: 34px;
  margin-top: 13px; }

.entry__desc {
  margin-top: 4px;
  opacity: 0.8;
  color: #ffffff;
  font-family: Roboto;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px; }

.entry__social-list {
  display: flex; }

.entry__icon {
  width: 31px;
  height: 17px;
  margin: 34px 20px 43px 6px; }
  .entry__icon:last-child {
    margin-right: 0; }

.entry__svg {
  fill: #ffffff;
  transition: .4s;
  opacity: 0.5; }
  .entry__svg--vk {
    width: 31px;
    height: 17px; }
  .entry__svg--github {
    width: 26px;
    height: 22px; }
  .entry__svg--in {
    width: 24px;
    height: 22px; }
  .entry__svg:hover {
    fill: #4e8839;
    opacity: 1; }

.entry__svg--github {
  width: 26px;
  height: 22px; }

.entry__tab-list {
  display: flex;
  flex-direction: row; }

.entry__tab-elem {
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  flex: 1;
  height: 45px;
  background-color: #6c9c5a;
  border-right: solid 2px;
  border-color: rgba(222, 227, 231, 0.5);
  transition: background-color .4s; }
  .entry__tab-elem:first-child {
    flex: 6; }
  .entry__tab-elem:nth-child(2) {
    flex: 5; }
  .entry__tab-elem:last-child {
    border-right: solid 0;
    flex: 4; }
  .entry__tab-elem:hover {
    background-color: #4e8839; }

.entry__tabs {
  width: 100%; }

.entry__tab-link {
  text-decoration: none;
  width: 100%;
  color: #ffffff;
  font-family: Roboto;
  font-size: 16px;
  font-weight: 500;
  line-height: 45px; }

.welcome__footer {
  position: absolute;
  bottom: 40px;
  height: 17px;
  color: #ffffff;
  font-family: Roboto;
  font-size: 16px;
  font-weight: 400; }
